@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;600&display=swap);
:root {
  /* --bg-color: #1c262f;
  --bg-color--card: #0a0c11;
  --fs-color--primary: #eef3f9;
  
  --fs-color--grey: #9396a5; */
  --gradient: linear-gradient(135deg, #aa26e9 40%, #5151e5 100%);
  --main-color: #aa26e9;
  --dark-grey: #212529;
  --light-grey: #f7f7f7;
  --fs-color--light: #fff;
  --border-radius--card: 1rem;
  --border-radius--btn: 0.25rem;
  --shadow--card: rgb(0 0 0 / 10%) 0px 3px 6px;
  --fs--body: 1.6rem;
  --fs--brand: 2.5rem;
  --fs--nav: 1.8rem;
  --fw--light: 400;
  --fw--regular: 700;
  --fw--bold: 900;
}

html {
  font-size: 62.5%;
}

body {
  width: 100%;
  height: 100vh;
  font-size: 1.6rem;
  font-size: var(--fs--body);
}


.btn-gradient {
  border: none;
  background-image: var(--gradient);
  border-radius: var(--border-radius--btn);
  color: var(--fs-color--light);
  font-weight: var(--fw--regular);
  padding: 0.5rem 2rem;
}

.imgcard-container {
  border: none;
  border-radius: var(--border-radius--card);
  box-shadow: var(--shadow--card);
  overflow: hidden;
  min-height: 38rem;
  position: relative;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.imgcard-container::after {
  position: absolute;
  content: '';
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 1;
}

.imgcard-container:hover {
  box-shadow: 0 6px 20px rgb(0 0 0 / 45%);
  transition: all 0.3s;
}

.imgcard-content {
  z-index: 2;
  color: var(--fs-color--light);
  height: 100%;
  flex: none;
}

.imgcard-content button {
  margin: 1rem 0;
}

.imgcard-content p {
  margin: 0 auto;
  max-width: 25rem;
}
.imgcard-content h1 {
  text-transform: capitalize;
}

.bg-spacecraft,
.bg-astronauts,
.bg-news {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.bg-spacecraft {
  background-image: url(/static/media/spacex.20366ef9.jpg);
}

.bg-astronauts {
  background-image: url(/static/media/astronaut.f6fd7689.jpg);
}

.bg-news {
  background-image: url(/static/media/news.afd4c685.jpg);
}

@media (max-width: 992px) {
  .img-container p {
    max-width: 100%;
  }
}


.summary-link--spacecraft,
.summary-link--spacecraft:hover {
  text-decoration: none;
  color: inherit;
}

.summary-card--spacecraft {
  border-radius: var(--border-radius--card);
  box-shadow: var(--shadow--card);
  overflow: hidden;
  margin: 3rem 0;
  text-align: center;
  cursor: pointer;
}

.summary-card--spacecraft:hover {
  box-shadow: 0 5px 15px rgb(0 0 0 / 25%);
  transition: all 0.3s;
}

.summary-card--spacecraft .card-title {
  font-size: 1.6rem;
  margin: 1rem 0 1.5rem 0;
}

.summary-card--spacecraft button {
  margin-bottom: 1rem;
}

.img-sm--spacecraft {
  max-height: 13rem;
  object-fit: cover;
  object-position: center;
}

@media (max-width: 768px) {
  .img-sm--spacecraft {
    max-height: 20rem;
  }
}

button:disabled {
  opacity: 0.3;
}
.btn-icon {
  border: none;
  background-color: transparent;
  color: var(--dark-grey);
  border-radius: var(--border-radius--btn);
  font-size: 3rem;
  padding: 0;
}

.btn-goback {
  background-color: var(--dark-grey);
  border-radius: var(--border-radius--btn);
  color: var(--fs-color--light);
  font-size: 1.6rem;
  padding: 0.5rem 1.5rem;
  display: flex;
  align-items: center;
}

.btn-goback svg {
  margin-right: 0.5rem;
}

.carousel {
  margin-top: 1.5rem;
}
.carousel-control-next,
.carousel-control-prev {
  align-items: flex-start;
  height: 1.5rem;
  top: -5rem;
  width: 0%;
  padding: 2.5rem;
}
.carousel-control-prev {
  left: 0%;
}

.pagenation {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: var(--light-grey);
  border-radius: 2rem;
  width: 10rem;
  padding: 0.5rem 1rem;
}

.carousel-inner {
  margin-top: 1rem;
}

.summary-link--astronauts,
.summary-link--astronauts:hover {
  text-decoration: none;
  color: inherit;
}

.summary-card--astronauts {
  border-radius: var(--border-radius--card);
  box-shadow: var(--shadow--card);
  overflow: hidden;
  margin: 2rem 0;
  text-align: center;
  cursor: pointer;
  min-height: 35rem;
}

.summary-card--astronauts:hover {
  box-shadow: 0 5px 15px rgb(0 0 0 / 25%);
  transition: all 0.3s;
}

.summary-card--astronauts .card-title {
  font-size: 1.6rem;
  /* margin: 0 0 1.5rem 0; */
}

.summary-card--astronauts button {
  margin: 1rem 0;
}

.img-sm--astronauts {
  /* max-height: 13rem; */
  object-fit: contain;
  object-position: top;
}

@media (max-width: 768px) {
  .img-sm--astronauts {
    max-height: 20rem;
  }
}

.alert-icon {
  font-size: 3rem;
}

.spacecraft-heading {
  padding: 1.5rem 1rem;
}

.loading {
  width: 4rem;
  height: 4rem;
  margin: 10rem auto;
}

.spacecraft-details {
  max-height: 50rem;
  overflow: auto;
}

.img-lg {
  width: 100%;
  max-height: 25rem;
  object-fit: cover;
  object-position: center;
}

.badges {
  margin-bottom: 1rem;
}

.col-btn {
  max-width: 3.5rem;
  padding: 0;
}

.page {
  text-align: center;
  background-color: var(--light-grey);
  border-radius: var(--border-radius--btn);
}

/* flex-direction: column;
  gap: 1.5rem;
  padding: 3.5rem 0;
  max-height: 65rem;
  overflow: auto; */

.astronauts-heading {
  padding: 1.5rem 1rem;
}

.search * {
  font-size: 1.6rem;
}

.clear--search {
  cursor: pointer;
}

@media (max-width: 768px) {
  .search {
    margin-bottom: 1rem;
  }
}

.filter {
  font-size: 1.6rem;
}

.astroDetails-details {
  max-height: 50rem;
  overflow: auto;
}

.img-lg--astroDetails {
  width: 100%;
  max-height: 25rem;
  object-fit: contain;
  object-position: top;
}

.news-box {
  border-radius: var(--border-radius--card);
  box-shadow: var(--shadow--card);
  overflow: hidden;
}

.img-box--news {
  padding: 0;
}

.img-sm--news {
  height: 15rem;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.info-box--news {
  padding: 1.5rem 1rem;
}

.info-box--news * {
  font-size: inherit;
}

.info-box--news .card-title {
  font-size: 1.8rem;
  margin: 1rem 0;
}

.info-box--news .card-subtitle {
  margin: 1rem 0;
}

.updated {
  font-weight: var(--fw--light);
  margin: 1rem;
}

header {
  font-size: var(--fs--nav);
}

.navbar-brand {
  font-size: var(--fs--nav);
}

.navbar-brand svg {
  vertical-align: text-bottom;
  font-size: var(--fs--brand);
  margin-right: 1rem;
}

.navbar {
  padding: 1.5rem;
  z-index: 3;
}

.navbar-brand a,
.nav-container a {
  text-decoration: none;
  text-transform: capitalize;
  color: var(--fs-color--light);
}

.nav-container a {
  margin-left: 4rem;
}

.nav-container a:hover {
  color: var(--main-color);
  transition: all 0.3s;
}

@media (max-width: 768px) {
  .nav-container {
    margin-top: 2rem;
  }
}

main {
  min-height: 80vh;
}

.main-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.main-container a {
  text-decoration: none;
  color: inherit;
}

@media (max-width: 992px) {
  .main-container {
    position: static;
    top: 0%;
    left: 0%;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
  }
}

